import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Paper from "@material-ui/core/Paper";
import SidebarArticle from "./Dashboard/SideBarArticle";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { ReactComponent as FinnplusLogo} from "../svg/finnplus.svg";
import Grid from "@material-ui/core/Grid";
import Link from "@material-ui/core/Link";
import { createMuiTheme} from '@material-ui/core/styles';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    boxShadow: 'none',
    border: '1px solid #C4C4C4',
    minHeight: '100px',
    maxWidth: '760px',
  },
  text: {
    flexGrow: 1,
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '13px',
    lineHeight: '14px',
    letterSpacing: '1.25px',
    marginBottom: '0px',
    marginTop: '5px',
  },
  avatar: {
    width: '120px',
    height: '120px',
    background: 'white',
  },
  icon: {
    width: '120px',
    height: '120px',
  },
  mainText: {
    fontWeight: '500 !important',
    fontSize: '14px !important',
    color: '#000000',
    marginTop: '13px',
  },
  button: {
    marginTop: theme.spacing(2),
    color: '#021E72',
  },
  submit: {
    marginTop: theme.spacing(2),
    color: 'white',
    background: '#021E72',
  },
  borderButton: {
    borderColor: '#021E72',
  },
  title: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 500,
    fontSize: '20px',
    lineHeight: '16px',
    letterSpacing: '1.25px',
    marginTop: '-20px',
  },
  title2: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '20px',
    lineHeight: '24px',
  },
  header: {
    margin: '20px',
  },
  item: {
    margin: '50px',
  },
  buttonContainer: {
    minWidth: '300px',
  },
  profile: {
    maxWidth: '300px',
  },
  appBar: {
    zIndex: theme.zIndex.drawer +1,
    color: theme.palette.text.primary,
    background: '#F4F4F4',
    padding: '10px'
  },
  mobileProfile: {
    margin: 'auto',
    textAling: 'center',
    justifyItems: 'center',
    width: '80%',
  },
  centered: {
    margin: 'auto',
    textAlign: 'center',
  }
}));

export default function Profile() {
  const classes = useStyles();
  const theme = useTheme();
  const data = window.obj ? window.obj.user : {latestArticles: [], favoriteArticles: []};

  function DesktTopView() {
    return (

      <div className={classes.root}>
        <Grid container spacing={4} >
          <Grid item xs={12} className={classes.header}>
            <Link href='/dashboard'>
              <FinnplusLogo className={classes.logo}/>
            </Link>
          </Grid>

          <Grid item xs={3} className={classes.item}>
            <div className={classes.profile}>
              <h1 className={classes.title}>MY PROFILE</h1>
              <Avatar className={classes.avatar} alt={data.name}>
                <AccountCircleIcon className={classes.icon} htmlColor='#5F5F5F'/>
              </Avatar>
              <p className={clsx(classes.text, classes.mainText)}>{data.name}</p>
              <p className={classes.text}>{data.email}</p>
              <Button href='/profileEdit' variant='outlined' fullWidth className={clsx(classes.button, classes.borderButton)}>EDIT PROFILE</Button>
              <Button href='/logout' fullWidth className={classes.button}>LOG OUT</Button>
              <div className={classes.buttonContainer}>
                <p className={clsx(classes.text, classes.mainText)}>Subscription Plan</p>
                {
                  data.subscription_end ? <p className={classes.text}>Monthly subscription ends {data.subscription_end}</p> :
                  data.package_end ? <p className={classes.text}>Package plan, {data.package_end} articles left </p> :
                  data.tokens ? <p className={classes.text}>Single payment, {data.tokens} articles left </p> :
                  <p className={classes.text}>No active plan</p>
                }
                <Button fullWidth href='/payment' className={classes.submit}>RENEW SUBSCRIPTION</Button>
              </div>
            </div>
          </Grid>

          <Grid item xs={6} className={classes.item}>
            <h2 className={classes.title2}>Recent Reads</h2>
            <Paper className={classes.paper}>
              {data.latestArticles.map((artData, i) => (<SidebarArticle data={artData} key={artData.title}/>))}
            </Paper>
            <h2 className={classes.title2}>Favourites</h2>
            <Paper className={classes.paper}>
              {data.favoriteArticles.map((artData, i) => (<SidebarArticle data={artData} key={artData.title}/>))}
            </Paper>
          </Grid>
          <Grid item xs={3}></Grid>
        </Grid>
      </div>

    )
  }

  function MobileView() {
    function TabPanel(props) {
      const { children, value, index, ...other } = props;

      return (
        <Typography
          component="div"
          role="tabpanel"
          hidden={value !== index}
          id={`simple-tabpanel-${index}`}
          aria-labelledby={`simple-tab-${index}`}
          {...other}
        >
          {value === index && <Box p={3}>{children}</Box>}
        </Typography>
      );
    }
    function a11yProps(index) {
      return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
      };
    }
    const [value, setValue] = React.useState(0);
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
    return (
      <div className={classes.root}>
        <AppBar position="static" className={classes.appBar} >
          <Link href='/dashboard'>
            <FinnplusLogo className={classes.logo}/>
          </Link>
          <Tabs value={value} onChange={handleChange} variant="fullWidth" indicatorColor='primary'>
            <Tab label="Profile" {...a11yProps(0)} />
            <Tab label="Recent" {...a11yProps(1)} />
            <Tab label="Favorites" {...a11yProps(2)} />
        </Tabs>
        </AppBar>

        <TabPanel value={value} index={0}>
          <div className={classes.mobileProfile}>
            <Avatar className={clsx(classes.centered, classes.avatar)} alt={data.name}>
              <AccountCircleIcon className={classes.icon} htmlColor='#5F5F5F'/>
            </Avatar>
            <p className={clsx(classes.centered, classes.text, classes.mainText)}>{data.name}</p>
            <p className={clsx(classes.centered, classes.text)}>{data.email}</p>
            <Button href='/profileEdit' variant='outlined' fullWidth className={clsx(classes.button, classes.borderButton)}>EDIT PROFILE</Button>
            <Button href='/logout' fullWidth className={classes.button}>LOG OUT</Button>
            <div>
              <p className={clsx(classes.centered, classes.text, classes.mainText)}>Subscription Plan</p>
              {
                data.subscription_end ? <p className={clsx(classes.centered, classes.text)}>Monthly subscription ends {data.subscription_end}</p> :
                data.package_end ? <p className={clsx(classes.centered, classes.text)}>Package plan, {data.package_end} articles left </p> :
                data.tokens ? <p className={clsx(classes.centered, classes.text)}>Single payment, {data.tokens} articles left </p> :
                <p className={clsx(classes.centered, classes.text)}>No active plan</p>
              }
              <p><Button fullWidth href='/payment' className={clsx(classes.centered, classes.submit)}>RENEW SUBSCRIPTION</Button></p>
            </div>
          </div>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Paper className={classes.paper}>
              {data.latestArticles.map((artData, i) => (<SidebarArticle data={artData} key={artData.title}/>))}
          </Paper>
        </TabPanel>
        <TabPanel value={value} index={2}>
          <Paper className={classes.paper}>
            {data.favoriteArticles.map((artData, i) => (<SidebarArticle data={artData} key={artData.title}/>))}
          </Paper>
        </TabPanel>
      </div>
    )
  }

  return (
    <div>
      { useMediaQuery('(min-width:800px)') ? <DesktTopView/> : <MobileView/>}
    </div>
  )
}