import React from 'react';
import clsx from 'clsx';
import AppBar from "@material-ui/core/AppBar";
import { makeStyles } from '@material-ui/core/styles';
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import {AccountCircle} from "@material-ui/icons";
import { ReactComponent as FinnplusLogo} from "../../svg/finnplus.svg";
import ProfileCard from "./ProfileCard";
import Popover from "@material-ui/core/Popover";
import { Typography } from '@material-ui/core';


const useStyles = makeStyles(theme => ({
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    zIndex: theme.zIndex.drawer +1,
    color: theme.palette.text.primary,
    background: '#F4F4F4',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  grow: {
    flexGrow: 1,
  },
  profileCardContainer: {
    width: '370px'
  },
  text: {
    fontSize: '12px'
  }
}));

export default function Bar(props) {
  const classes = useStyles();
  const open = props.open === undefined ? true : props.open;
  const userData = props.user;
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleProfileMenuOpen = event => {
    console.log('open');
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const profileCardOpen = Boolean(anchorEl);
  const id = profileCardOpen ? 'simple-popover' : undefined;

  return (
    <AppBar
      position="fixed"
      className={classes.appBar}
    >
      <Toolbar>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          onClick={props.onClick}
          edge="start"
          className={clsx(classes.menuButton, open && classes.hide)}
        >
          <MenuIcon />
        </IconButton>
        <IconButton
          onClick={props.onClick}
          color="inherit"
          edge="start"
          className={clsx(classes.menuButton, !open && classes.hide)}>
          <ChevronLeftIcon />
        </IconButton>
        <FinnplusLogo/>
        <div className={classes.grow}/>
        <Typography>
          {
            userData.subscription_end ? <p className={classes.text} id="appbar_info" >Monthly subscription<br/>ends {userData.subscription_end}</p> :
            userData.package_end ? <p className={classes.text} id="appbar_info">Package plan<br/>{userData.package_end} articles left </p> :
            userData.tokens ? <p className={classes.text} id="appbar_info" >Single payment<br/>{userData.tokens} articles left </p> :
            <p className={classes.text} id="appbar_info" >No active plan</p>
          }
        </Typography>
        <div>
          <IconButton
            edge="end"
            aria-label="account of current user"
            aria-haspopup="true"
            onClick={handleProfileMenuOpen}
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <Popover
            id={id}
            open={profileCardOpen}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            className={classes.popover}
          >
            <div className={classes.profileCardContainer}/>
            <ProfileCard data={userData}/>
          </Popover>
        </div>
      </Toolbar>
    </AppBar>
  );
}