import React from 'react';
import clsx from "clsx";
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Article from "./article";
import DrawerMenu from "./Dashboard/Drawer";
import Bar from "./Dashboard/AppBar";

import {Paper} from "@material-ui/core";
import SidebarArticle from "./Dashboard/SideBarArticle";
import Grid from "@material-ui/core/Grid";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Button from '@material-ui/core/Button';

import SubjectIcon from '@material-ui/icons/Subject';
import EuroSymbolIcon from '@material-ui/icons/EuroSymbol';
import RowingIcon from '@material-ui/icons/Rowing';
import DeveloperBoardIcon from '@material-ui/icons/DeveloperBoard';
import FitnessCenterIcon from '@material-ui/icons/FitnessCenter';
import LocalMoviesIcon from '@material-ui/icons/LocalMovies';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import FavoriteIcon from '@material-ui/icons/Favorite';

import { ReactComponent as NewsLogo } from '../svg/news.svg';
import { ReactComponent as OtherLogo } from '../svg/other.svg';
import { ReactComponent as WaldoLogo } from '../svg/waldo.svg';



const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    flexWrap: 'nowrap',
  },
  drawerHeader: {
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,

  },
  content: {
    margin: 'auto',
    flexGrow: 1,
    padding: theme.spacing(2),
    maxWidth: 1500,
  },
  paper: {
    boxShadow: 'none',
    border: '1px solid #C4C4C4',
  },
  articleContainer: {
    maxWidth: '750px',
    flexShrink: 1,
  },
  recentContainer: {
    minWidth: '300px',
    maxWidth: '600px',
    width: '25vw',
  },
  sideBarDummy: {
    width: '300px',
    flexShrink: 0,
  },
  logo: {
    maxWidth: '36px',
    maxHeight: '36px',
  },
  heading: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '24px',
    lineHeight: '32px',
  },
  newsHeader: {
    paddingLeft: theme.spacing(2)
  },
  recentHeader: {
    paddingLeft: '0px',
    marginBottom: '60px',
  },
  button: {
    marginLeft: '16px'
  }
}));


// TODO: Fetch categories and providers from server
export default function DBoard() {
  const classes = useStyles();
  const [catFilter, setCatFilter] = React.useState('All News');
  const [favFilter, setFavFilter] = React.useState(false);
  const cats = [
    {name: 'All News', icon: <SubjectIcon/>},
    {name: 'Politics', icon: <AccountBalanceIcon/>},
    {name: 'Sports', icon: <RowingIcon/>},
    {name: 'Economy', icon: <EuroSymbolIcon/>},
    {name: 'Technology', icon: <DeveloperBoardIcon/>},
    {name: 'Health', icon: <FitnessCenterIcon/>},
    {name: 'Entertainment', icon: <LocalMoviesIcon/>},
    {name: 'Favorites', icon: <FavoriteIcon/>}
    ];
  const providers = [
    {name: 'News', icon: <NewsLogo className={classes.logo}/>, link: '/news'},
    {name: 'The Other News', icon: <OtherLogo className={classes.logo}/>, link: '/other'},
    {name: 'Waldo News', icon: <WaldoLogo className={classes.logo}/>, link: '/waldo'},
    ];
  const articles = window.obj ? window.obj.articles : [];
  const userdata = window.obj ? window.obj.user : {};
  const matches = useMediaQuery('(max-width:750px)')
  const sideBarDummyVisible = useMediaQuery('(min-width:1300px)')
  const [open, setOpen] = React.useState((window.innerWidth >= 1300));

  function catFilterRule(el) {
    if (catFilter === 'All News') return true;
    return (el.name === catFilter)
  }

  function favFilterRule(el) {
    return el.fav
  }

  const handleDrawerToggle = () => {
    if (open) {
      setOpen(false);
    } else {
      setOpen(true)
    }
  };

  function handleSidebarClick(param) {
    if (param === 'Favorites') {
      setCatFilter('All News');
      setFavFilter(true);
      return
    }
    setFavFilter(false);
    setCatFilter(param)
  }

  return (
    <div className={classes.root}>
      <CssBaseline />

      <Bar open={open} user={userdata} onClick={handleDrawerToggle}/>
      <DrawerMenu open={open} categories={cats} providers={providers} onClick={handleSidebarClick}/>

      <main className={classes.content}>
        <div className={classes.drawerHeader}/>
        <Grid container className={classes.root} spacing={2} justify="center">

          { sideBarDummyVisible && <Grid item className={classes.sideBarDummy}/> }
          <Grid item className={classes.articleContainer} style={{minWidth: matches ? '100%':'700px'}}>
            {!favFilter && articles.filter(catFilterRule).map((obj, i) => (
              <div key={obj.name + " div"}>
                <h3 key={obj.name} className={clsx(classes.heading, classes.newsHeader)}>{obj.name}</h3>
                { obj.name != "" &&
                <div>
                <Button className={classes.button}>New</Button>
                <Button className={classes.button}>Popular</Button>
                <Button className={classes.button}>Rising</Button>
                <Button className={classes.button}>Local</Button>
                </div>
                }
                {obj.content.map((artData, i) => (<Article data={artData} key={artData.link}/>))}
              </div>
            ))}
            {favFilter &&
              <div key="favorites">
                <h3 key="favs" className={clsx(classes.heading, classes.newsHeader)}>Favorites</h3>
                {articles.map((obj, i) => (
                  <div>
                    {obj.content.filter(favFilterRule).map((artData, i) => (<Article data={artData} key={artData.link}/>))}
                  </div>
                ))}
              </div>
            }

          </Grid>
          { useMediaQuery('(min-width:1200px)') &&
          <Grid item className={classes.recentContainer}>
            <h3 className={clsx(classes.heading, classes.recentHeader)}>Recent reads</h3>
            <Paper className={classes.paper}>
              {userdata.latestArticles.map((artData, i) => (<SidebarArticle data={artData} key={artData.title}/>))}
            </Paper>
          </Grid>
          }
        </Grid>
      </main>
    </div>
  );
}